.quote{
  padding: 30px;
  background: var(--purple-grad-v1);
  border-radius: 2px;
  position: relative;
  margin: 0 0 20px 0;
  //box-shadow: $shadow-text-3;
  //&::before{
  //  pointer-events: none;
  //  content: 'â€Ÿ';
  //  font-size: 65px;
  //  line-height: 65px;
  //  transform: scale(1, -1);
  //  height: 18px;
  //  color: var(--white);
  //  position: absolute;
  //  display: inline-block;
  //  top: 45px;
  //  left: 30px;
  //}
  //&::after{
  //  pointer-events: none;
  //  content: 'â€Ÿ';
  //  font-size: 65px;
  //  line-height: 65px;
  //  transform: scale(-1, 1);
  //  height: 18px;
  //  color: var(--white);
  //  position: absolute;
  //  display: inline-block;
  //  top: 55px;
  //  left: 52px;
  //}
  &__text{
    font-size: 15px;
    line-height: 30px;
    letter-spacing: 0.01em;
    font-style: italic;
    color: var(--white);
  }
  &__info{
    display: flex;
    flex-direction: column;
    &-wrap{
      margin: 0 0 20px 0;
      display: flex;
      align-items: center;
      //padding: 0 0 0 60px;
    }
    &-image{
      width: 60px;
      height: 60px;
      display: flex;
      overflow: hidden;
      align-items: center;
      justify-content: center;
      border: 1px solid $salad;
      border-radius: 4px;
      margin: 0 12px 0 0;
    }
    &-user{
      &-name{
        font-size: 15px;
        line-height: 20px;
        color: var(--white);
      }
      &-date{
        font-size: 11px;
        line-height: 20px;
        color: var(--white);
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .quote{
    border-radius: 0;
    margin: 0 0 20px 0;
  }
}

@media screen and (max-width: 600px) {
  .quote{
    padding: 30px 15px;
    &::before{
      right: 48px;
      left: auto;
    }
    &::after{
      right: 25px;
      left: auto;
    }
    &__info{
      &-wrap{
        margin: 0 0 20px 0;
        padding: 0 60px 0 0;
      }
    }
    &__text{
      font-size: 13px;
      line-height: 28px;
    }

  }
}